import React, { useState } from 'react'
import './Contact.css'
import {useHistory} from "react-router-dom"


function Contact() {
    const [chatername, setChatername] = useState(null)
    const history=useHistory()
    const handleChat = (e) => {
        e.preventDefault()
        const btnCh = document.querySelectorAll(".chatlink")
        btnCh.forEach(btCh => btCh.click())
    }
    return (
        <div>
            <div className="contact-area">
                <h3 className="text-white text-center">Contact me</h3>
                <div className="contact-main-ar">
                    <div className="contact-txt-area">
                        <span className="mailandnumber">
                            <div className="call-us">
                                <i class="fas fa-phone-alt"></i>
                                <p><span>Call Us </span><br /> +918589840073</p>
                            </div>
                            <div className="email-us">
                                <i class="far fa-envelope"></i>
                                <p><span>Email Us</span><br /> arshadayanikkal@gmail.com</p>
                            </div>
                        </span>
                        <button className="chat-me" data-bs-toggle="modal" data-bs-target="#chatModal">
                            <i class="fas fa-headset"></i> Chat with Me
                        </button>
                    </div>
                    <div className="contact-form-area">
                        <div className="contact-form-content">
                            <form id="contact-fm" action="">
                                <span className="nameAndemail">
                                    <div className="contact-form-feeld name-feeld">
                                        <input placeholder="Name" name="name" type="text"  required />
                                    </div>
                                    <div className="contact-form-feeld email-feeld">
                                        <input placeholder="Email" name="email" type="email"  required />
                                    </div>
                                </span>
                                <div className="contact-form-feeld subject-feeld">
                                    <input placeholder="Subject" name="subject" type="text"  required />
                                </div>
                                <div className="contact-form-feeld message-feeld">
                                    <textarea name="" id="" name="message" cols="30" rows="10" placeholder="Message" required></textarea>
                                </div>
                                <button type="submit" className="contact-form-feeld submit-button">
                                    Send Message
                                </button>
                            </form>

                        </div>
                    </div>
                    <a href={`https://api.whatsapp.com/send/?phone=918589840073&text=Hello,%20my%20Name%20is%20${chatername}%20,%20I%20want%20your%20services`} className="chatlink">dfdf</a>
                    {/* model starting div */}
                    <div class="modal fade" id="chatModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div class="modal-dialog modal-dialog-centered">
                            <div class="modal-content">
                                <div class="modal-heade ">
                                    {/* <h5 class="modal-title" id="exampleModalLabel">Modal title</h5> */}
                                    <button type="button" class="btn-close close-bts bg-white" data-bs-dismiss="modal" aria-label="Close"></button>
                                </div>
                                {/* <div class="modal-body">
                                    ...
                                </div> */}
                                <form onSubmit={handleChat}>

                                    <p className="text-center text-white display-6">Chat with me</p>
                                    <div className="chat-name-feeld ">
                                        <input type="text" onChange={(e) => setChatername(e.target.value)} value={chatername} placeholder="Name" required />
                                    </div>
                                    <div className="chat-name-feeld chat-now-btn">
                                        <button type='submit' className="">Chat now</button>
                                    </div>




                                </form>
                                {/* <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                    <button type="button" class="btn btn-primary">Save changes</button>
                                </div> */}
                            </div>
                        </div>
                    </div>
                    {/* model ending div */}
                </div>
            </div>
        </div>
    )
}

export default Contact
