import React from 'react'
import './Dialogbox.css'
import {useHistory} from "react-router-dom"
function Dialogbox() {
    const history=useHistory()
    return (
        <div>
            <div className="dialogbox-area">
                        <div className="dialog-btns m-auto">
                            <button onClick={() => { history.push('/services') }} className="all-service-btn text-center">All Services</button>
                            <button onClick={()=>{history.push("/contact")}} className="contact-me-btn text-center">Contact Me</button>
                        </div>
                        <p className="text-center dialogbox-1-p">I'm available for freelance projects</p>
                        <p className="text-center dialogbox-2-p">Let's work together indeed!</p>
            </div>
        </div>
    )
}

export default Dialogbox
