import React, { useState } from 'react'
import './Skill.css'

function Skill() {
    const [skills1, setSkills1] = useState([{ sk: "HTML", cl: "html" }, { sk: "CSS", cl: "css" }, { sk: "JavaScript", cl: "javascript" },
    { sk: "Node.js", cl: "node" }, { sk: "React", cl: "react" }, { sk: "C Programing", cl: "c" },
    { sk: "Python", cl: "python" }, { sk: "MongoDB", cl: "mongodb" }, { sk: "Google Firebase", cl: "firebase" }])
   
    return (
        <div>
            <div className="skill-area">
                <p className="skill-head-txt text-center">PROFESSIONAL SKILLS</p>
                <div className="skill-bar-area">
                    {/* start bar */}

                    <div className="p-4 skill-bars">
                        
                        <div className="bars-area m-auto row">
                                        {skills1.map(items=>{
                                            return<div className="onebar col-sm-6 col-md-4 col-6 ">
                                            <div className="bar" data-aos="fade-up"
                                            data-aos-anchor-placement="top-bottom">
                                            <div className="info" >
                                            <span>{items.sk}</span>
                                            </div>
                                            <div className={`progress-line ${items.cl}`}>
                                            <span></span>
                                            </div>
                                            </div>
                                            </div>
                                        })}

                        </div> 

                    </div>

                    {/* end bar */}
                </div>
            </div>
        </div>
    )
}

export default Skill
