import React from 'react'
import './Navbar.css'
import {useHistory} from "react-router-dom"

function Navbar() {
    const history=useHistory()
    return (
        <div>
            <div className="nav-area">
                <div className="nav-contents ">
                    <div className="logo col-6">
                        <p onClick={()=>{history.push("/")}} className="logo-text">.Coder</p>
                        <p className="bar" >
                            {/* {nav ? <i class="fas fa-bars"></i> : <i class="far fa-times"></i>} */}
                        </p>
                    </div>


                    <div className="nav-links-area " id="bar">
                        <span className="nav-links">
                            <li onClick={()=>{history.push("/")}}>Home</li>
                            <li onClick={() => { history.push('/services') }}>Services</li>
                            <li onClick={()=>{history.push("/contact")}}>Contact</li>
                        </span>
                    </div>
                    {/* ### */}
                    <div className="b-ar">
                    <input type="checkbox" id="active"/>
                    <label for="active" class ="menu-btn"><i className ="fas fa-bars"></i></label>
                    <div className ="wrapper">
                    <ul>
                    <span className="nav-links text-center">
                            <li onClick={()=>history.push('/')}><a href="#">Home</a></li>
                            <li><a href="#" onClick={() => { history.push('/services') }}>Services</a></li>
                            <li><a href="#" onClick={()=>{history.push("/contact")}}>Contact</a></li>
                        </span>
                    </ul>
                    </div>
                    </div>
                {/* ### */}
                </div>

            </div>
        </div>
    )
}

export default Navbar
