import React from 'react'
import "./Allservices.css"
import {useHistory} from "react-router-dom"

function Allservices() {
    const history=useHistory()
    const Allserives=[{title:"Web Development",dis:"We Target on E-commerce and Service Management Web Application with admin Panel & payment services",icon:"fas fa-database"},
    {title:"Web Design",dis:"We are Providing a Customized Website that's Perfect for your Business or Personal Site",icon:"fas fa-laptop-code"},
    {title:"UI/UX Design",dis:"We design every element on the user interface-icons, buttons, typography, to create an intuitive experience for your users",icon:"fas fa-mobile-alt"},
    {title:"Bug Hunting",dis:"Finding Web Application Bugs. Mainly targeting OWASP Top vulnerabilities like code injection, Authentication bypass, and Remote Code Execution, etc...",icon:"fas fa-bug"},
    {title:"Network  Security_Test",dis:"Penetration Testing, Vulnerability Assessment, loT Network Security Testing (Protect your Network from hackers)",icon:"fas fa-user-secret"},
    {title:"Digital Design",dis:"Designing Advertisement Poster, Book Cover, Logos, and other Branding and Communication Materials.",icon:"fas fa-fill-drip"}

]
    return (
        <div>
            <div className="allservices-area">
                <h4 className="text-center">All Services</h4>
                <h3 className="text-center">What I do for you</h3>

            <div className="services-boxs-area row">
               {Allserives.map(list=>{
                   return <div className="services-card  col-sm-4 col-md-4 col-xs-6">
                   <div className="service-card-content">
                   <span class="card-span">
                   <i class={list.icon}></i>
                   <h2>{list.title}</h2>
                   <p>{list.dis}</p>
                   </span>
                   </div>
               </div>
               })}
            </div>
            <div onClick={()=>history.push('/contact')} className="contact-me-allSer"><button >Contact Me</button></div>
            </div>
            
        </div>
    )
}

export default Allservices
